const getFood = ({ name, rating, startPrice, text }) => {
  //Формирование секции ресторана
  return `
    <section class="section-restaraunts">
    <div class="section-title">
      <h2 class="heading">${name}</h2>
      <div class="other-info">
        <span class="rating"><img src="./img/star.svg" alt="star" />${rating}</span>
        <span class="info price">${startPrice} Р</span>
        <span class="info name">${text}</span>
      </div>
    </div>
    <div class="food-cards"></div>
  </section>
    `;
};

const createCard = ({ src, head, ingridients, price = 0, delay, alt }) => {
  //Создание карточек товаров
  return `
  <div class="card wow fadeInUp" data-wow-delay="${delay}s">
  <img src="${src}" class="dish-item" alt="${alt}" />
  <div class="food-info">
    <div class="order-title">
      <h2 class="heading">${head}</h2>
      <span class="ingridients">${ingridients}</span>
    </div>
    <div class="order-info">
      <button class="button order-btn">
        <span class="btn-text">В корзину</span
        ><img
          src="./img/shopping-cart-white.svg"
          alt="shopping-cart"
        />
      </button>
      <span class="order-price">${price} P</span>
    </div>
  </div>
</div>  
  `;
};

const createCartItem = (head, price) => {
  //Создание item в корзине
  return `
  <div class="food-row">
  <span class="food-name">${head}</span>
  <div class="counter-block">
    <span class="price">${price} P</span>
    <button class="counter-btn minus">-</button>
    <span class="amounth">1</span>
    <button class="counter-btn plus">+</button>
    <button class="modal-btn delete">&times;</button>
  </div>
  </div>
  `;
};

const createCardsTemplates = (cardsArr) => {
  return cardsArr.map((item) => createCard(item));
};

const setCards = () => {
  const key = sessionStorage.key(sessionStorage.length - 1).toLowerCase();
  const cards = document.querySelector(".food-cards");
  const cardsTemplates = createCardsTemplates(cardsObj[key]);
  cardsTemplates.forEach((item) =>
    cards.insertAdjacentHTML("afterbegin", item)
  );
  const addButtons = cards.querySelectorAll(".order-btn");
  return [addButtons, key];
};

const setMainPrice = () => {
  let smallPrices = Array.from(modalBody.querySelectorAll(".price"));
  let result;
  if (smallPrices.length == 0) {
    result = 0;
  } else {
    result = smallPrices
      .map((item) => parseInt(item.textContent))
      .reduce((total, item) => total + item);
  }
  finalPriceElement.innerHTML = `${result} P`;
};

const calculatePrice = (target, startPrice, item) => {
  let priceValue = 0;
  let price = item.querySelector(".price");
  let amounth = item.querySelector(".amounth");
  let amounthValue = parseInt(amounth.textContent);
  if (target.classList.contains("minus")) {
    amounthValue <= 1 ? (amounthValue = 1) : amounthValue--;
    priceValue = startPrice * amounthValue;
  }
  if (target.classList.contains("plus")) {
    amounthValue++;
    priceValue = startPrice * amounthValue;
  }
  price.textContent = `${priceValue} P`;
  amounth.textContent = amounthValue;
  setMainPrice();
};

const getPrice = (head, key) => {
  const arr = cardsObj[key];
  let result;
  arr.forEach((item) => {
    for (let prop in item) {
      if (item[prop] == head) {
        result = item.price;
      }
    }
  });
  return result;
};

const changeBtnState = (btnArrOrBtn, flag, global) => {
  if(!global){
    if (flag == true) {
      btnArrOrBtn.firstElementChild.textContent = "В корзине";
      btnArrOrBtn.style.backgroundColor = btnArrOrBtn.style.borderColor = "#5ab247";
      btnArrOrBtn.disabled = true;
      btnArrOrBtn.lastElementChild.src='./img/checkmark.png';
      return btnArrOrBtn;
    } else {
      btnArrOrBtn.firstElementChild.textContent = "В корзину";
      btnArrOrBtn.style.backgroundColor = btnArrOrBtn.style.borderColor = "#1890FF";
      btnArrOrBtn.disabled = false;
      btnArrOrBtn.lastElementChild.src='./img/shopping-cart-white.svg';
    }
  }else{
      for(let item of btnArrOrBtn){
        item.firstElementChild.textContent = "В корзину";
        item.style.backgroundColor = item.style.borderColor = "#1890FF";
        item.disabled = false;
        item.lastElementChild.src='./img/shopping-cart-white.svg';
      }
  }
};

const setPrice = (arrPrices, head, key, selectedBtn) => {
  let finalPrice = arrPrices.reduce((total, price) => total + price);
  finalPriceElement.innerHTML = `${finalPrice} P`;
  const counterBlocks = modalBody.querySelectorAll(".counter-block");
  const item = counterBlocks[counterBlocks.length - 1];
  item.addEventListener("click", function (event) {
    const target = event.target;
    let startPrice = parseInt(getPrice(head, key));
    if (target.classList.contains("counter-btn")) {
      calculatePrice(target, startPrice, this);
    }
    if (target.classList.contains("delete")) {
      arrPrices.splice(
        arrPrices.findIndex((item) => item === startPrice),
        1
      );
      changeBtnState(selectedBtn, false,false);
      target.parentElement.parentElement.remove();
      if (modalBody.children.length == 1) {
        emptyCartMessage.style.display = "flex";
      }
      setMainPrice();
    }
  });
};

const setItemInCart = ([arrButtons, key]) => {
  for (let item of arrButtons) {
    item.addEventListener("click", function () {
      const head = this.parentElement.previousElementSibling.firstElementChild
        .textContent;
      const price = parseInt(this.nextElementSibling.textContent);
      emptyCartMessage.style.display = "none";
      const selectedBtn = changeBtnState(this, true,false);
      modalBody.insertAdjacentHTML("beforeend", createCartItem(head, price));
      prices.push(price);
      counterItem++;
      itemCounter.textContent = counterItem;
      setPrice(prices, head, key, selectedBtn);
    });
  }
};

$(document).ready(() => {
  const data = JSON.parse(
    sessionStorage.getItem(sessionStorage.key(sessionStorage.length - 1))
  );
  mainRestaraunts.insertAdjacentHTML("afterbegin", getFood(data));

  const addButtons = setCards();
  setItemInCart(addButtons);
});

const showModal = () => {
  modalWindow.classList.add("open-modal");
};

const closeModal = () => {
  modalWindow.classList.remove("open-modal");
  const modalBodyLenght = modalBody.children.length - 1;
  itemCounter.textContent = counterItem = modalBodyLenght;
};

const sendOrder = (order) => {
  const modalBodyLenght = modalBody.children.length;
  const allButtons=mainRestaraunts.querySelectorAll('.order-btn');
  prices.splice(0);
  changeBtnState(allButtons,null,true);
  for (let i = 1; i < modalBodyLenght; i++) {
    modalBody.removeChild(modalBody.lastElementChild);
  }
  finalPriceElement.textContent = `0 P`;
  emptyCartMessage.style.display = "flex";
  swal({
    icon: "success",
    title: "Заказ принят",
    button: true,
  });
  // console.log(order);
};

const getOrderInfo = () => {
  const final = parseInt(finalPriceElement.textContent);
  let headings = [];
  const rowItemArr = [...modalBody.querySelectorAll(".food-row")];
  if (final !== 0 && rowItemArr.length !== 0) {
    headings = rowItemArr.map((item) => {
      return {
        [item.firstElementChild.textContent]: `${parseInt(
          item.lastElementChild.firstElementChild.textContent
        )} (${parseInt(
          item.lastElementChild.querySelector(".amounth").textContent
        )})`,
      };
    });
    sendOrder({ orderInfo: headings, finalPrice: final });
  } else {
    swal({
      icon: "error",
      title: "Корзина пустая",
      button: true,
    });
  }
};

finishOrder.addEventListener("click", getOrderInfo);

basketBtn.addEventListener("click", showModal);

cancelBtn.addEventListener("click", closeModal);

greyBtn.addEventListener("click", closeModal);

new WOW().init();
